.programs {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0 2rem;
}

.programs-header {
    display: flex;
    gap: 7rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
    padding: 3rem;
}

.program-categories {
    gap: 4rem;
    justify-content: center;
    
}

.card:hover {
    background: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);;
    cursor: pointer;
}

.card {
    display: flex;
    flex-direction: column;
    background-color: gray;
    padding: 2rem;
    gap: 1rem;
    color: white;
    justify-content: space-between;
    
}

.card>:nth-child(1) {
    width: 4rem;
}

card>:nth-child(2) {
    font-size: 1rem;
    font-weight: bold;
    
}

card>:nth-child(3) {
    font-size: 0.9rem;
    line-height: 25px;
}
.card-title{
    justify-content: center;
    display: flex;
    font-weight: bold;
}
@media screen and (max-width: 785px){
    .programs-header{
        gap: 3rem;
    }
    .stroke-text{
        font-size: 1rem;
    }
    #sc{
        font-size: 1rem;
        width: 9rem;
    }
   
    
}