.testimonials{
    display: flex;
    gap: 1 rem;
    padding: 0 2rem;

}
.left-t{
    display: flex;
    flex: 1 1;
    gap: 2rem;
    flex-direction: column;
    text-transform: uppercase;
    color: white;
}
.left-t>:nth-child(1){
    color: orange;
    font-weight: bold;
}
.left-t>:nth-child(2),.left-t>:nth-child(3){
font-weight: bold;
font-size: 3rem;
}
.left-t>:nth-child(4){
    text-transform: none;
    text-align: justify;
    letter-spacing: 2px;
    line-height: 40px;
}
.right-t{
    flex: 1;
    position: relative;
}
.right-t>img{
    position: absolute;
    width: 22rem;
    height: 21rem;
    object-fit: cover;
    right: 8rem;
    top: 2rem;
}
.arrows>img{
    display: flex;
    gap: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 3rem;
    
}
.right-t>:nth-child(1){
    position: absolute;
    width: 23rem;
    height: 20rem;
    border: 4px solid orange;
    background-color: transparent;
    right: 9rem;
    top: 0.9rem;
}
.right-t>:nth-child(2){
    position: absolute;
    width: 21rem;
    height: 20rem;
    right: 7rem;
    top: 4rem;
    background: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);;
}
.arrows>img{
    width: 3rem;
    cursor: pointer;
   
    
}
#arr-r>img{
    margin: 1rem;
    width: 2.2rem;
    top: 21.1rem;
    left: 5rem;
    position:relative;
}
@media screen and (max-width: 1495px) {

#arr-r>img{
   
    top: 25rem;
    position: relative;
    left: 7rem;

}
}
@media screen and (max-width: 1160px){
    .right-t>img {
    left: 5rem;
    width: 19rem;
    height: 19rem;
    top: 5rem;
    }
    .right-t>:nth-child(1){
        left: 3rem;
    width: 19rem;
    top: 4rem;
    left: 4rem;
    }
    .right-t>:nth-child(2) {
    width: 18rem;
    top: 5rem;
    left: 7rem;
    }
}
@media screen and (max-width: 785px){
    .testimonials{
        display: grid;
        text-align: center;
    }
    /* .right-t{
        margin-top: 34rem;
        left: -16rem;
    } */
    .left-t>:nth-child(2){
        display: contents;

    }
    .right-t{
        margin-top: 1rem;
        left: 8px;
    }
    
}
@media screen and (max-width: 400px){
    .right-t>:nth-child(1){
        left: 0rem;
    }
    .right-t>:nth-child(2){
        left: 3rem;
    }
    .right-t>img{
        left: 1rem;
    }
}