.Reasons{
    padding: 0 2rem;
    display: flex;
    gap: 2rem;
}
.left-r{
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3 , 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
    height: 25rem;
}
.left-r>img{
    object-fit: cover;
}
.left-r>:nth-child(1){
    width: 29rem;
    grid-row: 1/3;
    height: 26.5rem;
    
}
.left-r>:nth-child(2){
    width: auto;
    height: 16rem;
    grid-column: 2/4;
}
.left-r>:nth-child(3){
    width: 14rem;
    grid-row: 2;
    
}
.left-r>:nth-child(4){
    width: 9rem;
    height: 9.2rem;
    grid-row: 2;
    
}
.right-r{
    flex: 1 1;
    text-transform: uppercase;
    gap: 1rem;
    display: flex;
    flex-direction: column;
}
.right-r>span{
    font-weight: bold;
    color: orange;
}
.right-r>div{
    color: white;
    font-size: 3rem;
    font-weight: bold;
}
.deatails-r{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.deatails-r>div{
    display: flex;
    font-size: 1rem;
    gap: 1rem;
}
.deatails-r>div>img{
    width: 2rem;
    height: 2rem;
}
@media screen and (max-width: 1160px){
    .left-r>:nth-child(1) {
        width: 15rem;
        height: 16rem;
    }
    .left-r>:nth-child(2) {
        width: 17rem;
    }
    .left-r>:nth-child(3) {
       width: 9rem;
       left: -8rem;
       position: relative;
    }
    .left-r>:nth-child(4) {
    height: 6rem;
    width: 7rem;
    position: relative;
    left: -8rem;
    }
}
@media screen and (max-width: 785px){
    .right-r{
    margin-left: -33rem;
    justify-content: center;
    margin-top: -41rem;
    }
    .left-r>:nth-child(1){
    width: 15rem;
    height: 16rem;
    top: -12rem;
    position: relative;
    }
    .left-r>:nth-child(2) {
        position: relative;
        top: -12rem;
    }
    .left-r>:nth-child(3){
        position: relative;
        top: -12rem;
    }
    .left-r>:nth-child(4) {
        position: relative;
        top: -12rem;
    }
    .left-r {
        margin-top: 36rem;
        justify-items: center;
    }
    @media screen and (max-width: 400px){
        .left-r>:nth-child(1){
            left: 3rem;
            height: 16rem;
        }
        .left-r>:nth-child(2){
            left: -14rem;
            margin-top: 17rem;
        }
        .left-r>:nth-child(3) {
            left: -13rem;
        }
        .left-r>:nth-child(4){
            left: -14rem;
        }
    }
}