.join{
display: flex;
padding: 0 2rem;
gap: 10rem;
}
.left-j{
    color: white;
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    position: relative;
}
.left-j>hr{
    position: absolute;
    width: 10.5rem;
    border: 2px solid orange;
    border-radius: 20%;
    margin: -10px;
}
.left-j>div{
    display: flex;
    gap: 1rem;
}
.email-container{
    display: flex;
    gap: 4rem;
    background-color: gray;
    padding: 1rem 2rem;
    right: -16rem;
    position: relative;
    top: 2rem;
}
.email-container>input{
    background-color: transparent;
    border: none;
    outline: none;
    color: lightgray;
}
::placeholder{
    color: lightgray;
}
.btm-j{
    background-color: orange;
    color: white;
}
@media screen and (max-width: 1495px) {
 #j{
    font-size: 2rem;
 }   
 .email-container {
    right: -12rem;
  }
}
@media screen and (max-width: 1160px){
    #level{
    font-size: 38px;
    top: 12px;
    position: relative;
    }
    #re {
        width: 13rem;
        top: 1rem;
        position: relative;
    }
    #j {
            font-size: 29px;
            width: 11rem;
            top: 1rem;
            position: relative;
        }
    #wh{
        top: 1rem;
    }
    .left-j {
        font-size: 2rem;
    }
    .email-container {
        right: 5rem;
    }
    .btm-j{
        width: 96px
    }
}
@media screen and (max-width: 785px){
    .join{
        padding: 25rem 2rem;
    }
    .left-j{
        text-align: center;
    }
    .left-j>div{
        text-align: center;
        justify-content: center;
    }
    #re{
        font-size: 2rem;
    }
    #wh{
        font-size: 2rem;
    }
    .email-container{
        margin-top: 12rem;
        right: 39rem;
    }
}
@media screen and (max-width: 400px){
    #div1{
    left: -5rem;
    position: relative;
    gap: 0rem;
    }
    .left-j>div{
        gap: 0rem;
       left: -2rem;
       position: relative;
    }
    .email-container{
        left: -40rem;
        gap: 0rem;
    }

}