.hero{
    display: flex;
    justify-content: space-between;
}
.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
}
.right-h{
    flex: 1;
    position: relative;
    background-color: orange;
}
.the-best-ad{
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    
}
.the-best-ad>span{
    z-index: 2;
}
.the-best-ad div{
    position: absolute;
    background-color: orange;
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
    z-index: 1;
    animation: animate 5s linear infinite;
}
@keyframes animate {
  0%{
    left: 0;
  }
  50%{
    left: 71%;
  }
  0%{
    left: 0;
  }
}
.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}
.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;
  }
  .figures{
    display: flex;
    gap: 2rem;
    padding: 2rem 0;
  }
  .figures>div{
    display: flex;
    flex-direction: column;
  }
  .figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
  }
  .figures>div>span:nth-of-type(2){
    color: gray;
    text-transform: uppercase;
  }

  .hero-buttons{
    display: flex;
    gap: 1rem;
    font-weight: normal;
  }
  .hero-buttons>:nth-child(1){
    color: white;
    background-color: orange;
    width: 8rem;
  }
  .hero-buttons>:nth-child(2){
    color: white;
    background-color: transparent;
    width: 8rem;
    border: 2px solid orange;
  }
  .right-h>.btm{
    position: absolute;
    right: 3rem;
    top: 2rem;
    color: black;
  }
  .heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: black;
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: 38px;
    position: absolute;
    right: 10rem;
    top: 2rem;
    
  }

  .heart-rate>img{
    height: 4rem;
    width: 5rem;
  }
  .heart-rate>:nth-child(2){
    color: gray;
    
  }
  .heart-rate>:nth-child(3){
    color: white;
    font-weight: bold;
    font-size: 1.5rem;

  }
  .hero-image{
    position: absolute;
    top: 8rem;
    right: 8rem;
    width: 34rem;
  }
  .calories{
    display: flex;
    gap: 2rem;
    background-color:#656565 ;
    border-radius: 5px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top: 39rem;
    right: 40rem;
    animation: ani 5s linear ;
  }
  @keyframes ani {
    0%{
      right: 0;
    }
    50%{
      right: 100%;
    }
    0%{
      right: 0;
    }
  }

  .calories>img{
    width: 3rem;
  }
  .calories>div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1.5rem;
  }
  .calories>div>span{
    width: 8rem
  }
  .calories > div > :nth-child(1){
    color: black;
  } 
  .calories > div > :nth-child(2){
    color: white;
    font-size: 1.5rem;
  } 
  .blur-h{
    width: 31rem;
    height: 20rem;
    left: 22px;
    top: 8rem;
  }
  @media screen and (max-width: 1495px) {
      .calories{
        right: 27rem;
        top: 45rem;
      }  
      .heart-rate{
        left: -4rem;
      }
      .heart-rate>img{
        height: 20px;
        width: 29px;
        left: 29px;
        position: relative;
      }
      .heart-rate>:nth-child(3) {
        font-size: 18px;
      }
      .hero-text>div:nth-of-type(3) {
        font-size: 14px;
        width: 35rem;
        height: 5rem;
      }
      .stroke-text {
        width: 18rem;
       font-size: 2rem;
      }
      #sc{
        font-size: 2rem;
      }
      #left{
        left: -2rem;
        top: 26.3rem;
      }
      
  }
  @media screen and (max-width: 1160px) {
    .heart-rate {
      left: -4rem;
      top: 9rem;
    }
    .right-h>.btm {
      top: 2rem;
    width: 7rem;
    }
    .navbar-toggler{
      left: 45rem;
    position: relative;
    }
    .hero-image {
    width: 24rem;
    top: 13rem;
    left: -23rem;
    }
    .stroke-text>Y{
    font-size: 42px;
    left: 2rem;
    position: relative;
    }
    .calories{
      left: -23rem;
    }
  }
