  @media screen and (max-width: 785px){
    .hero{
      flex-direction: column;
    }
    .blur-h{
      width: 14rem;
    }
    .the-best-ad{
      margin-top: 5rem;
      transform: scale(0.8);
      left: 7rem;
      top: -2rem;
    }
    .navbar-toggler{
      left: 24rem;
