
.plans{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
#cd{
    /* background-color:linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%); ; */
    background-color: orange;
    transform: scale(1.1);
}

.p-blur-1{
    width: 28rem;
    height: 23rem;
    top: 133rem;
    left: 109px;
}
.p-blur-2{
    width: 28rem;
    height: 23rem;
    top: 133rem;
    right: 109px;
}
@media screen and (max-width: 1160px){
 
  #sc{
    font-size: 26px;
  }
  .plans {
    gap: 26px;
  }
  #sc{
    top: -2rem;
    position: relative;
  }
  .stroke-text{
    width: 18rem;
    top: -1rem;
    position: relative;
  }
 
}
@media screen and (max-width: 785px){
    .plans-container{
      margin-top: -15rem;
    }
    .plans{
      gap: 4rem;
    display: grid;
    }
    
}
@media screen and (max-width: 400px){
  .plans-container{
    margin-top: 0rem;
  }
  .p-blur-1{
    width: 0rem;
  }
  .p-blur-2 {
    width: 0rem;
  }
}